<template>
    <div class="container">
        <h1>hello world</h1>
        <Header
            @toggle-add-task-view="toggleAddTaskView"
            :show_add_task="showAddTasksView"
        />
        <div v-show="showAddTasksView">
            <AddTask
                :now_tasks_length="tasks.length"
                @add-new-task="addNewTask"
            />
        </div>
        <Tasks
            @toggle-reminder="toggleReminder"
            :tasks="tasks"
            @delete-task="deleteTask"
        />
    </div>
</template>

<script>
import Header from "./components/Header";
import Tasks from "./components/Tasks";
import AddTask from "./components/AddTask.vue";
export default {
    name: "App",
    components: {
        Header,
        Tasks,
        AddTask,
    },
    data() {
        return {
            tasks: [],
            showAddTasksView: false,
        };
    },
    methods: {
        deleteTask(id) {
            if (confirm("Are you sure?")) {
                this.tasks = this.tasks.filter((item) => item.id !== id);
            }
        },
        toggleReminder(id) {
            console.log(id);

            let tasks = [...this.tasks];
            let target_idx = tasks.findIndex((item) => item.id == id);
            let target_reminger = tasks[target_idx].reminder;
            tasks[target_idx].reminder = !target_reminger;
            this.tasks = tasks;
        },
        addNewTask(new_task) {
            this.tasks = this.tasks.concat(new_task);
        },
        toggleAddTaskView() {
            this.showAddTasksView = !this.showAddTasksView;
        },
    },
    created() {
        this.tasks = [
            {
                id: 1,
                text: "Doctors Appointment",
                day: "March 1st at 2:30pm",
                reminder: true,
            },
            {
                id: 2,
                text: "Meeting at School",
                day: "March 3rd at 1:30pm",
                reminder: true,
            },
            {
                id: 3,
                text: "Food Shopping",
                day: "March 3rd at 11:00am",
                reminder: false,
            },
        ];
    },
};
</script>

